@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,300&display=swap");

.App {
    // text-align: center;
    font-family: "Roboto";
    letter-spacing: 0.1rem;
  }

  .container_pay {
    display: flex;
    flex-direction: row;
    margin: 5% auto;
    width: 635px;
    height: 430px;
    background: white;
    box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.5);
  }

  .item {
    width: 50%;
    position: relative;
  }

  .item-image {
    height: 430px;
    width: 100%;
    object-fit: cover;
  }

  .overlay-effect {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: 0.2;
    background-color: #303030;
    overflow: hidden;
    z-index: 1;
  }

  .Modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
  }
  
  .Overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
  }

  .item-details {
    position: absolute;
    bottom: 0;
    margin-bottom: 5px;
    margin-left: 20px;
    color: #84a17d;
    text-align: left;
  }

  .item-details__title {
    font-size: 22px;
  }
  
  .item-details__amount {
    font-weight: bolder;
  }

  .checkout {
    background: #84a17d; /* fallback for old browsers */
  
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 430px;
    width: 50%;
  }

  .checkout-form {
    padding: 20px 20px;
  }
  
  .checkout-field {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
  }

  .checkout-field label {
    text-align: left;
    color: #e0eafc;
    font-size: 10px;
    margin-bottom: 5px;
    text-transform: uppercase;
    letter-spacing: 0.1rem;
  }

 
  #name {
    background-color: transparent;
    border: 1px solid #cecece;
    border-radius: 20px;
    color: #e0eafc;
    height: 35px;
    width: 280px;
    font-size: medium;
    color: #303030;
    // background-color: red;
  }

  #email {
    background-color: transparent;
    border: 1px solid #cecece;
    border-radius: 20px;
    color: #e0eafc;
    height: 35px;
    width: 280px;
    font-size: medium;
    color: #303030;
    // background-color: red;
  }

  #phone {
    background-color: transparent;
    border: 1px solid #cecece;
    border-radius: 20px;
    color: #e0eafc;
    height: 35px;
    width: 280px;
    font-size: medium;
    color: #303030;
    // background-color: red;
  }
  
  .paystack-button {
    cursor: pointer;
    text-align: center;
    font-size: 10px;
    letter-spacing: 0.1rem;
    text-transform: uppercase;
    background-color: #bfbfbf;
    font-weight: bold;
    color: #e0eafc;
    border: none;
    border-radius: 5px;
    width: 100%;
    height: 25px;
    margin-top: 40px;
  }